import React from "react";
import { Link } from "react-router-dom";
import useSettings from "../Hooks/useSettings";
import { Head } from "./Head";
import styles from "./Produtos.module.css";

const Produtos = () => {
  const [produtos, setProdutos] = React.useState(null);
  const { loading, setLoading, erro, setErro } = useSettings();

  React.useEffect(() => {
    setLoading(true);
    async function fetchProdutos() {
      try {
        const response = await fetch(
          "https://ranekapi.origamid.dev/json/api/produto"
        );
        if (response.ok) {
          const json = await response.json();
          setProdutos(json);
        } else {
          setErro("Ocorreu um erro, tente novamente reiniciando a página!");
        }
      } finally {
        setLoading(false);
      }
    }
    fetchProdutos();
  }, [setErro, setLoading]);

  if (loading) {
    return (
      <>
        <Head
          title="MiniStore"
          description="Essa é uma mini loja de produtos importados"
        />
        <div className="loading"></div>
      </>
    );
  } else {
    if (produtos === null)
      return (
        <div>
          <Head
            title="MiniStore"
            description="Essa é uma mini loja de produtos importados"
          />
          <h1 style={{ fontSize: "2em" }} className="animeLeft">
            {erro}
          </h1>
        </div>
      );
    return (
      <section className={`${styles.produtos} animeLeft`}>
        <Head
          title="MiniStore"
          description="Essa é uma mini loja de produtos importados"
        />
        {produtos &&
          produtos.map((produto) => (
            <Link to={`produto/${produto.id}`} key={produto.id}>
              <img src={produto.fotos[0].src} alt={produto.fotos[0].titulo} />
              <h1 className={styles.nome}>{produto.nome}</h1>
            </Link>
          ))}
      </section>
    );
  }
};

export default Produtos;
